<template>

    <div>
        <van-nav-bar
                title="各大电商平台购物券"
                fixed="true"
                left-text="返回"
                right-text="分享链接"
                left-arrow
                @click-left="onClickLeft"
                @click-right="onClickRight"
        />

        <iframe src="http://www.tzbridge.cn/mall/?sign=1qCb3" style=" margin-top: 50px;border: 0px" :width="width" :height="height"></iframe>


    </div>
</template>

<script>
    import { Dialog } from 'vant';
    export default {
        name: "FaceRecognition",
        components:{
            Dialog
        },
        data () {
            return {
                videoEle: null,
                width:'500px',
                height:'800px'
            }
        },
        created(){
            this.width = window.innerWidth-10;
            this.height = window.innerHeight-20 ;
        },
        methods:{
            onClickLeft() {
                this.$router.back()
            }
            , onClickRight() {

                var href=window.location.href;
                this.$copyText( href).then(function (e) {
                    Dialog.alert({
                        title: '',
                        message: '链接复制成功'
                    }).then(() => {
                        // on close
                    });
                    console.log(e)
                }, function (e) {
                    alert('Can not copy')
                    console.log(e)
                })

            }
        }

    }
</script>

